<template>
  <div id="terminal-background">
    <div id="terminal">
      <div class="commend">
        <b class="terminal-user">leegeunhyeok@lgh-com</b>
        :~$ 
        <input type="text" v-model="commend" @keypress="onKeypress" id="terminal-input">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      terminal: null,
      commend: ''
    }
  },
  mounted() {
    this.terminal = document.getElementById('terminal');
    document.getElementById('terminal-input').focus();
  },
  methods: {
    onKeypress(e) {
      if(e.charCode === 13 && this.commend.length) {
        this.commendExecute();
      }
    },
    commendExecute() { //   
      const $commend = this.commend; //    
      this.commend = ''; //  

      //   
      var result = document.createElement('div');
      result.classList.add('commend'); //  

      var user = document.createElement('b');
      user.classList.add('terminal-user');
      user.appendChild(document.createTextNode('leegeunhyeok@lgh-com'));
      
      result.appendChild(user); //     
      result.appendChild(document.createTextNode(' :~$ ' + $commend)); //    
      result.appendChild(document.createElement('br')); //   

      //          
      switch($commend.toLowerCase()) {
        case 'help': {
          result.appendChild(document.createTextNode('help: '));
          break;
        }
        default: { //     
          result.appendChild(document.createTextNode($commend + ': commend not found'));
        }
      }
      this.terminal.insertBefore(result, this.terminal.lastChild); //      (    )
      this.autoScroll();
      document.getElementById('terminal-input').focus();
    },
    autoScroll() { //   
      var terminal = document.getElementById('terminal-background');
      terminal.scrollTop = terminal.scrollHeight;
    }
  }
}
</script>

<style>
#terminal-background {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  overflow-y: auto;
}

#terminal {
  color: #fff;
}

.terminal-user {
  color: lawngreen;
}

.commend input {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  color: #fff;
  font-size: 1rem;
}
</style>




